.footer {
  background-image: url(../images/footer-bg.png);
  background-size: cover;
  height: 200px;
  background-position: 65%;
  position: sticky;
  bottom: 0;
  z-index: -1;

  @include onTablet {
    background-position: center;
    height: 480px;
  }

  @include onDesktop {
    height: 555px;
  }
}
